@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Oi&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.home__logo{font-family:'Black Han Sans', sans-serif;color:white;font-size:50px}.home__links{display:flex;justify-content:space-between;width:150px}.home__links>*{all:unset;cursor:pointer;color:#fff}

.container{display:flex;justify-content:space-evenly;align-items:center;width:100%;height:100px;background-color:#191970}

.login__container a{all:unset;cursor:pointer;color:#fff}.login__form{display:flex;flex-direction:column;margin:50px auto;width:200px}.login__form>*{margin-bottom:25px}

.signup__container a{all:unset;cursor:pointer;color:#fff}.signup__form{display:flex;flex-direction:column;margin:50px auto;width:200px}.signup__form>*{margin-bottom:25px}

.dashboard__container{margin:0 auto;width:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.dashboard__btn-logout,.dashboard__link-contacts{all:unset;cursor:pointer;color:#fff}.dashboard__container p{color:white}

.form__info{display:flex;flex-direction:column;margin:50px auto}.form__info>*{margin-bottom:20px}.form__date{display:flex;flex-direction:column;align-items:flex-start}.form__date label{font-size:12px;margin-bottom:5px}.form__container p{color:black}

.subform__contacts{display:flex;flex-direction:column}.subform__contacts>*{margin-bottom:20px}.subform__mobile,.subform__phone{display:flex;flex-direction:column;align-items:flex-start}.subform__mobile label,.subform__phone label{font-size:12px;margin-bottom:5px}

.contacts__container{margin:0 auto;width:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.contacts__link{all:unset;cursor:pointer;color:#fff}.contacts__table{margin-top:50px}.contacts__table,tr,td{border:1px solid #d3d3d3;border-collapse:collapse;padding:10px}.contacts__table a{text-decoration:none}.contacts__sort{display:flex;align-items:center}.contacts__sort p{color:white;margin-right:5px}

.pagination__container{display:flex;flex-direction:column;margin:50px auto;width:40%;display:flex;place-items:center}.pagination{display:flex;place-items:center}.pagination__num{margin:0 10px;list-style:none;cursor:pointer;display:flex;place-content:center;place-items:center;width:30px;height:30px;border:1px solid midnightblue;border-radius:30px}.pagination__num a{text-decoration:none}.pagination__control{cursor:pointer}.pagination__sort{display:flex;place-items:center}.pagination__sort p{margin-right:5px}

.details__container{margin:0 auto;width:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.details__container a{all:unset;cursor:pointer;color:#fff}.details__container button{margin-top:20px;cursor:pointer}.details__table{margin-top:30px}.details__table,th,tr{border:1px solid #d3d3d3;border-collapse:collapse;padding:10px}

.update__container{margin:0 auto;width:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.update__container a{all:unset;cursor:pointer;color:#fff}.update__form{display:flex;flex-direction:column;margin:50px auto}.update__form>*{margin-bottom:20px}

.favorites__container{margin:0 auto;width:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.favorites__link{all:unset;cursor:pointer;color:#fff}.favorites__table{margin-top:50px}.favorites__table,tr,td{border:1px solid #d3d3d3;border-collapse:collapse;padding:10px}.favorites__table a{text-decoration:none}

